@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  line-height: 1; }

body {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji !important; }

img {
  vertical-align: middle; }

ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

* {
  font-family: "Roboto", sans-serif; }

#Tmato .Tmato {
  height: 100vh;
  width: 100%;
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-content: center; }

.PlayButton {
  position: absolute;
  z-index: 3;
  width: 540px;
  height: 540px;
  border-radius: 50%;
  border: 4px solid #ff4384;
  right: 21%;
  top: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer; }
  .PlayButton:active {
    top: 20.5%;
    right: 20.8%; }
  .PlayButton .ButtonContent {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 500px;
    height: 500px;
    border-radius: 50%;
    background-color: #ff4384;
    border: 4px solid #ff4384; }
    .PlayButton .ButtonContent img {
      width: 96px;
      height: auto; }

.Tmato_content {
  width: 65%;
  height: 100%;
  background-color: #ffedf7;
  padding: 50px 85px; }
  .Tmato_content .addmission {
    width: 70%;
    height: 56px;
    background-color: #ffffff;
    padding: 18px;
    padding-left: 16px;
    display: flex;
    justify-content: center;
    align-content: center; }
    .Tmato_content .addmission input {
      display: inline-block;
      width: 100%;
      height: 100%;
      border: none;
      color: #ff4384;
      font-size: 16px; }
      .Tmato_content .addmission input::placeholder {
        color: #ff4384;
        font-size: 16px; }
      .Tmato_content .addmission input:focus {
        outline: none; }
    .Tmato_content .addmission span {
      display: inline-block;
      line-height: 20px;
      font-size: 24px;
      color: #ff4384;
      font-weight: bold;
      cursor: pointer; }
  .Tmato_content .UpcomingList {
    margin-top: 150px;
    width: 70%; }
    .Tmato_content .UpcomingList ul li.List {
      width: 100%;
      height: 35px;
      border-bottom: 1px solid #003164;
      margin-bottom: 10px; }
      .Tmato_content .UpcomingList ul li.List .ListContent .RoundButton {
        width: 24px;
        height: 24px; }
      .Tmato_content .UpcomingList ul li.List .ListContent .ListText {
        font-size: 16px; }
    .Tmato_content .UpcomingList ul li .ListTime {
      font-size: 176px;
      color: #ff4384;
      font-weight: bold;
      text-align: center;
      margin-bottom: 100px; }
    .Tmato_content .UpcomingList ul li .ListContent {
      display: flex;
      align-items: center; }
      .Tmato_content .UpcomingList ul li .ListContent .RoundButton {
        width: 48px;
        height: 48px;
        border-radius: 50%;
        border: 2px solid #003164;
        transition: background-color 0.3s;
        cursor: pointer; }
        .Tmato_content .UpcomingList ul li .ListContent .RoundButton:hover {
          background-color: #000; }
      .Tmato_content .UpcomingList ul li .ListContent .ListText {
        font-size: 48px;
        color: #003164;
        font-weight: bold;
        padding-left: 10px; }
      .Tmato_content .UpcomingList ul li .ListContent img {
        display: block;
        margin-left: auto; }

.Tmato_menu {
  width: 35%;
  height: 100%;
  background-color: #003164;
  padding: 50px 85px; }
  .Tmato_menu ul {
    height: 100%;
    width: 100%; }
    .Tmato_menu ul li {
      width: 100%;
      text-align: right;
      margin-bottom: 48px; }
      .Tmato_menu ul li img {
        width: 36px;
        height: 36px;
        cursor: pointer; }
      .Tmato_menu ul li.lineText {
        color: #ffffff;
        font-size: 24px;
        font-weight: bold;
        writing-mode: vertical-rl;
        margin-right: 85px; }
